import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ApiService {
  private baseUrl = 'https://tmpro-5265168c0988.herokuapp.com/api';

  constructor(private http: HttpClient) {}

  // ** Equipos **
  getTeams(): Observable<any[]> {
    return this.http.get<any[]>(`${this.baseUrl}/teams`, { withCredentials: true }).pipe(
      map(response => Array.isArray(response) ? response : []),
      catchError(error => {
        console.error('Error al obtener equipos:', error);
        return of([]); // Retorna un array vacío en caso de error
      })
    );
  }

  createTeam(team: any): Observable<any> {
    return this.http.post<any>(`${this.baseUrl}/teams`, team, { withCredentials: true });
  }

  updateTeam(id: number, team: any): Observable<any> {
    return this.http.put<any>(`${this.baseUrl}/teams/${id}`, team, { withCredentials: true });
  }

  deleteTeam(id: number): Observable<any> {
    return this.http.delete<any>(`${this.baseUrl}/teams/${id}`, { withCredentials: true });
  }

  // ** Jugadores **
  getPlayers(): Observable<any[]> {
    return this.http.get<any[]>(`${this.baseUrl}/players`, { withCredentials: true }).pipe(
      map(response => Array.isArray(response) ? response : []),
      catchError(error => {
        console.error('Error al obtener jugadores:', error);
        return of([]); // Retorna un array vacío en caso de error
      })
    );
  }

  createPlayer(player: any): Observable<any> {
    return this.http.post<any>(`${this.baseUrl}/players`, player, { withCredentials: true });
  }

  updatePlayer(id: number, player: any): Observable<any> {
    return this.http.put<any>(`${this.baseUrl}/players/${id}`, player, { withCredentials: true });
  }

  deletePlayer(id: number): Observable<any> {
    return this.http.delete<any>(`${this.baseUrl}/players/${id}`, { withCredentials: true });
  }

  registerUser(user: { username: string; password: string; role: string }): Observable<any> {
    return this.http.post(`${this.baseUrl}/auth/register`, user).pipe(
        catchError(error => {
            console.error('Error al registrar usuario:', error);
            return of(error.error);  // Devolver el error para manejarlo en el frontend
        })
    );
}


  // ** Login (sin JWT) ** 
  login(username: string, password: string): Observable<any> {
    const body = { username, password };
    return this.http.post(`${this.baseUrl}/auth/login`, body, { withCredentials: true });
  }

  // ** Logout **
  logout(): Observable<any> {
    return this.http.post(`${this.baseUrl}/auth/logout`, {}, { withCredentials: true });
  }
}
